<template>
  <div class="container">
    <div class="header">
      <div class="title">生成激活码</div>
      <div class="back" @click="hanleHide">
        <fa-icon icon-name="undo" />
        返回
      </div>
    </div>
    <el-divider />
    <div class="wrap">
      <el-row>
        <el-col :lg="16" :md="20" :sm="24" :xs="24">
          <el-form ref="addForm" v-loading="loading" :model="form" status-icon label-width="100px" @submit.native.prevent>
            <el-form-item label="用户权限" prop="auth">
              <el-select v-model="form.auth" placeholder="请选择">
                <el-option
                  v-for="item in 4"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="项目名称" prop="project_name">
              <el-input v-model="form.project_name" size="medium" placeholder="请填写项目名称"/>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submitForm('addForm')">保 存</el-button>
              <el-button @click="resetForm('addForm')">重 置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import cdKeyModel from '@/models/cdKey'
export default {
  name: 'KeyAdd',
  components: {},
  data() {
    return {
      loading: false,
      form: {
        auth: 1,
        project_name: ''
      },
    }
  },
  computed: {},
  created() { },
  mounted() { },
  methods: {
    hanleHide() {
      this.$emit('hanleHide', false)
    },
    async submitForm() {
      const res = await cdKeyModel.createOne(this.form)
      this.$message.success('生成激活码成功')
      this.hanleHide()
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-divider--horizontal {
    margin: 0;
  }
  .container {
    height: 100%
    .header {
      display flex
      justify-content space-between
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
      .back {
        margin-right 40px
        cursor pointer
      }
    }
    .wrap {
      padding: 20px;
    }
  }
</style>
