/**
 * 注册码模块
 */

import {
  post,
  get,
  put,
  _delete
} from '@/utils/request'

export default class CDKey {
  /** 生成注册码
   * @static
   */
  static async createOne(key) {
    const data = await post('/cms/cdkey/generate', key)
    return data
  }

  // 获取激活码列表
  static async getList(size, page) {
    const data = await get(`/cms/cdkey/list?page=${page}&size=${size}`)
    return data
  }

  static async deleteOne(cdkey) {
    const data = await _delete(`/cms/cdkey?cdkey=${cdkey}`)
    return data
  }

  // 改变激活码状态
  static async updateOne(cdkey, state) {
    const data = await put(`/cms/cdkey/state?cdkey=${cdkey}&state=${state}`)
    return data
  }

  // 管理员激活某个用户
  // static async activateCDKey(uid, cdkey) {
  //
  // }
}
